.navigation-wrapper {
  @media (max-width: $mobile-only-width) {
    position: fixed;
    top: 0;
    left: 13%;
    right: 0;

    display: flex;
    flex-direction: column;

    height: 70vh;
    padding: 100px 10px 50px 10px;

    font-size: 24px;
    line-height: 28px;
    background-color: #fff;

    &--open {
      overflow-x: scroll;
      visibility: visible;
      opacity: 1;
      transform: translateX(0);
      transition: transform 0.5s, opacity 0.3s, visibility 0s;
    }

    &--close {
      visibility: hidden;
      pointer-events: none;
      opacity: 0;
      transform: translateX(100%);
      transition: transform 0.5s, opacity 0.3s, visibility 0s 0.3s;
    }
  }
}

.navigation {
  @media (min-width: $tablet-width) {
    display: flex;
    align-items: center;
  }

  &__list {
    display: flex;

    @media (max-width: $mobile-only-width) {
      margin-bottom: 70px;
      flex-direction: column;
      font-weight: 700;
    }

    @media (min-width: $tablet-width) {
      column-gap: 18px;
    }

    @media (min-width: 850px) {
      column-gap: 21px;
    }

    @media (min-width: 950px) {
      column-gap: 30px;
    }

    @media (min-width: $desktop-width) {
      column-gap: 20px;
    }

    @media (min-width: $desktop-ultra-width) {
      column-gap: 40px;
    }
  }

  &__item {
    position: relative;

    @media (max-width: $mobile-only-width) {
      width: 100%;
      text-align: right;

      margin-bottom: 16px;
      border-bottom: 1px solid #eeeeee;

      @media (min-width: 500px) {
        text-align: center;
      }
    }

    @media (min-width: $tablet-width) {
      font-size: 12px;

      border-bottom: 1px solid transparent;

      &:hover {
        border-bottom: 1px solid #ffffff;
        transition: 0.3s;
      }

      &:active {
        opacity: 0.6;
        transition: 0.3s;
      }
    }

    @media (min-width: 850px) {
      font-size: 14px;
    }

    @media (min-width: $desktop-width) {
      font-size: 16px;
    }
  }

  &__link {
    @media (max-width: $mobile-only-width) {
      color: #000000;
    }

    @media (min-width: $tablet-width) {
      font-weight: 700;
      color: #fff;
    }
  }

  &__mobile-ticket-button {
    display: none;
    @media (max-width: $mobile-only-width) {
      display: block;
      padding: 16px 70px;
      font-weight: 600;
      text-align: center;

      align-self: flex-end;

      @media (min-width: 500px) {
        align-self: center;
      }
    }
  }

  &__buttons {
    display: flex;
    margin-left: auto;
    align-items: flex-start;
  }

  &__blind-button {
    width: 32px;
    margin-right: 24px;
  }

  &__blind-button:hover img {
    &:hover {
      outline: 1px solid #fff;
      border-radius: 50%;
    }

    &:active {
      opacity: 0.7;
      transition: 0.3s;
    }
  }

  //Кнопка покупки билета desktop
  &__ticket-button {
    padding: 8px 16px;
    font-size: 12px;
    line-height: 18px;
    @media (max-width: $mobile-only-width) {
      display: none;
    }
  }

  &__toggler {
    display: none;

    @media (max-width: $mobile-only-width) {
      position: relative;
      z-index: 999;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 32px;
      height: 32px;
      background-color: #000000;
      border-radius: 50%;
      border: 1px solid #fff;
      cursor: pointer;

      span {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 18px;
        height: 2px;
        background-color: #fff;
      }

      &::before,
      &::after {
        content: "";
        position: absolute;
        width: 18px;
        height: 2px;
        background-color: #fff;
      }

      &::before {
        top: 25%;
        transform: translateY(-25%);
      }

      &::after {
        bottom: 25%;
        transform: translateY(25%);
      }

      &.active span {
        transform: scale(0);
      }

      &.active::before {
        top: 48%;
        transform: rotate(-45deg) translateY(-48%);
      }

      &.active::after {
        bottom: 52%;
        transform: rotate(45deg) translateY(52%);
      }
    }
  }
}
