.header-slider {
  @media (max-width: $mobile-only-width) {
    margin-bottom: 40px;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 30px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 50px;
  }

  @media (min-width: $desktop-ultra-width) {
    margin-bottom: 80px;
  }
}
