.modal-covid {
  position: fixed;
  z-index: 998;

  background-color: #222;
  color: #fff;
  font-weight: 600;
  height: auto;

  @media (max-width: $mobile-only-width) {
    right: 1%;
    bottom: 5%;
    max-width: 343px;

    padding: 16px;
    border-radius: 4px;
  }

  @media (min-width: $tablet-width) {
    right: 20px;
    bottom: 5%;
    max-width: 308px;

    padding: 24px;
    border-radius: 12px;
  }

  &__close {
    position: absolute;
    top: 12px;
    right: 12px;
  }

  &__text {
    font-size: 16px;
    line-height: 24px;

    margin-bottom: 16px;
  }

  &__button {
    cursor: pointer;
    border: 1px solid #fff;
    padding: 8px 24px;

    font-size: 12px;
    line-height: 16px;

    &:hover {
      opacity: 0.7;
    }
  }
}

