.overlay--open {
  @media (max-width: $mobile-only-width) {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    filter: brightness(0.3);
    z-index: 999;
  }
}

.disable-scroll {
  @media (max-width: $mobile-only-width) {
    position: fixed;
    left: 0;
    top: 0;

    width: 100%;
    height: 100vh;

    overflow: hidden;
  }
}
