.slider {
  width: 100%;
  position: relative;

  &__slide {
    display: flex;
    flex-direction: column;
  }

  &__slide img {
    width: 100%;
    height: auto;
    object-fit: cover;

    @media (max-width: $mobile-only-width) {
      height: 572px;
    }

    @media (min-width: $tablet-width) {
      height: 450px;
    }

    @media (min-width: $desktop-width) {
      height: 550px;
    }

    @media (min-width: $desktop-ultra-width) {
      height: 680px;
    }
  }

  &__about {
    color: #ffffff;
    font-weight: 600px;

    @media (max-width: $mobile-only-width) {
      position: absolute;
      top: 50%;
      left: 35px;
      right: 16px;
      transform: translateY(-50%);
    }

    @media (min-width: $tablet-width) {
      position: absolute;
      max-width: 666px;
      top: 50%;
      left: 10%;
      right: auto;
      transform: translateY(-50%);
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    font-size: 12px;
    line-height: 16px;
  }

  &__item {
    padding: 8px 20px;
    background-color: #222;
    border-radius: 100px;
    margin-right: 8px;
    margin-bottom: 8px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__header {
    @media (max-width: $mobile-only-width) {
      font-size: 24px;
      line-height: 36px;
      font-weight: 900px;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 8px;
      font-size: 36px;
      line-height: 54px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 12px;
      font-size: 52px;
      line-height: 64px;
    }
  }

  &__author {
    margin-bottom: 20px;
    @media (max-width: $mobile-only-width) {
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__show-more {
    padding: 8px 24px;
  }
}

.swiper,
.cards-slider {
  position: relative;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}

.swiper-slide {
  transition: 0.5s;
}

.swiper-button-prev {
  color: #ffffff;
  display: flex;

  @media (max-width: $mobile-only-width) {
    left: 5px;
  }

  @media (min-width: $tablet-width) {
    left: 40px;
  }
}

.swiper-button-next {
  color: #ffffff;

  @media (max-width: $mobile-only-width) {
    right: 15px;
  }

  @media (min-width: $tablet-width) {
    right: 40px;
  }
}

.swiper-button-prev::after,
.swiper-button-next::after {
  @media (max-width: $mobile-only-width) {
    font-size: 16px;
  }
  font-size: 24px;
}

.swiper-pagination-bullet {
  background-color: gray;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.swiper-pagination-bullet-active {
  background-color: #222;
}
