.about {
  padding: 40px 16px;
  color: #fff;
  background: #222;
  overflow: hidden;

  @media (max-width: $mobile-only-width) {
    margin-bottom: 40px;
  }

  @media (min-width: $tablet-width) {
    padding: 30px 40px 0;

    margin-bottom: 60px;
  }

  &__header {
    display: flex;
    flex-direction: column;

    @media (max-width: $mobile-only-width) {
      margin-bottom: 20px;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 43px;

      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__title {
    font-size: 24px;
    line-height: 36px;
    font-weight: 700;

    margin-bottom: 16px;

    @media (min-width: $mobile-only-width) {
      font-size: 36px;
      line-height: 40px;

      margin-bottom: 0;
    }
  }

  &__buttons {
    display: flex;
    flex-wrap: wrap;

    gap: 16px;

    @media (min-width: $mobile-only-width) {
      gap: 20px;
    }
  }

  &__buttons a {
    font-size: 12px;
    line-height: 16px;

    padding: 8px 24px;
    border: 1px solid #fff;

    &:hover {
      transition: 0.3s;
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }

    @media (min-width: $tablet-width) {
      font-size: 16px;
      line-height: 24px;

      margin-right: 20px;
    }
  }

  &__info {
    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 1fr 2fr;
      column-gap: 50px;
    }
  }

  &__image {
    display: block;
    margin-bottom: 20px;

    @media (min-width: $tablet-width) {
      margin-left: 30px;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 24px;
    @media (min-width: $tablet-width) {
      p {
        display: block;
        margin-bottom: 20px;
      }
    }
  }

  &__text--full {
    @media (max-width: $mobile-only-width) {
      display: none;
    }
  }

  &__button-show-more {
    font-size: 16px;
    line-height: 24px;
    padding: 8px 24px;

    @media (min-width: $tablet-width) {
      display: none;
    }
  }
}
