* {
  box-sizing: border-box;
}

body {
  font-family: "Gilroy", "Arial", sans-serif;
  margin: 0 auto;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
}

.button {
  font-weight: 600;
  border-radius: 4px;
  color: #ffffff;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
div {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.button--red {
  background-color: #ad2717;

  &:hover {
    transition: 0.3s;
    background-color: #8b1a0d;
  }

  &:active {
    opacity: 0.6;
    transition: 0.3s;
  }
}
