.cards {
  //Возможный вариант реализации гридами
  // grid-template-columns: repeat(5, 280px);
  // grid-auto-rows: minmax(360px, auto);
  // gap: 16px;
  // &::-webkit-scrollbar {
  //   display: none;
  // }

  // @media (max-width: 767px) {
  //   grid-auto-flow: unset;
  //   overflow: scroll;
  // }
  display: flex;
  gap: 0;

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: minmax(480px, auto);
    gap: 28px;
  }
}

.poster-card {
  position: relative;
  border-radius: 4px;
  background-color: #222;
  color: #fff;

  @media (max-width: $mobile-only-width) {
    max-width: 280px;
    max-height: 360px;
  }

  @media (min-width: $tablet-width) {
    max-width: 480px;
    height: 400px;
  }

  @media (min-width: $desktop-width) {
    max-width: 100%;
    height: 480px;
    border-radius: 12px;

    &:nth-child(4) {
      grid-column: 1/3;
    }
    //max-height: auto; Корректное отображение превью
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 4px;

    @media (min-width: $tablet-width) {
      border-radius: 12px;
    }
  }

  &:hover img {
    filter: brightness(115%);
    transition: 0.3s;
    box-shadow: 0 0 3px #444;
  }

  &__about {
    position: absolute;
    bottom: 10%;
    padding: 0 16px;
    z-index: 100;

    @media (min-width: $desktop-width) {
      padding: 0 24px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    font-size: 12px;
    font-weight: 600;

    @media (max-width: $mobile-only-width) {
      margin-bottom: 16px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 28px;
    }
  }

  &__item {
    padding: 8px 20px;
    border-radius: 100px;
    background-color: #222;
  }

  &__info {
    font-weight: 400;

    @media (max-width: $mobile-only-width) {
      font-size: 12px;
      line-height: 16px;

      margin-bottom: 4px;
    }

    @media (min-width: $tablet-width) {
      font-size: 16px;
      line-height: 24px;

      margin-bottom: 8px;
    }
  }

  &__title {
    font-weight: 600;

    @media (max-width: $mobile-only-width) {
      font-size: 16px;
      line-height: 24px;

      margin-bottom: 16px;
    }

    @media (min-width: $tablet-width) {
      font-size: 18px;
      line-height: 28px;

      margin-bottom: 12px;
    }

    @media (min-width: $desktop-width) {
      font-size: 24px;
      line-height: 32px;

      margin-bottom: 16px;
    }
  }

  &__button {
    line-height: 24px;
    padding: 8px 24px;
  }
}
