.poster {
  position: relative;

  @media (max-width: $mobile-only-width) {
    padding: 0 16px;
    padding-bottom: 35px;
    margin-bottom: 9px;
  }

  @media (min-width: $tablet-width) {
    padding: 0 40px;
    padding-bottom: 35px;
    margin-bottom: 35px;
  }

  &__header {
    display: flex;
    justify-content: space-between;

    @media (max-width: $mobile-only-width) {
      margin-bottom: 22px;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 44px;
    }
  }

  &__title {
    font-weight: 700;
    @media (max-width: $mobile-only-width) {
      font-size: 24px;
      line-height: 36px;
    }

    @media (min-width: $tablet-width) {
      font-size: 36px;
      line-height: 40px;
    }
  }

  &__show-more {
    padding: 8px 24px;

    border-radius: 4px;
    border: 1px solid #000;
    color: #000000;

    &:hover {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }

    @media (max-width: $mobile-only-width) {
      font-size: 12px;
      line-height: 16px;
    }

    @media (min-width: $tablet-width) {
      font-size: 16px;
      line-height: 24px;
    }
  }
}
