.footer {
  padding: 26px 16px 16px;
  background-color: #222;

  @media (min-width: $tablet-width) {
    padding: 40px 40px 20px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 17px;
    row-gap: 36px;
    margin-bottom: 32px;

    @media (min-width: $tablet-width) {
      grid-template-columns: repeat(5, 1fr);
      margin-bottom: 36px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
  }

  &__link {
    position: relative;
    color: #fff;
    font-size: 12px;
    line-height: 16px;

    margin-bottom: 4px;

    &:hover {
      align-self: flex-start;
      //border-bottom: 1px solid #fff;-макет
      transition: 0.3s;
      color: #8b1a0d; // работа как на сайте
    }

    &:active {
      opacity: 0.6;
    }

    @media (min-width: $desktop-width) {
      font-size: 14px;
    }
  }

  &__head-link {
    font-weight: 600;
    margin-bottom: 10px;

    @media (min-width: $tablet-width) {
      font-size: 16px;
      margin-bottom: 12px;
    }
  }

  &__social {
    @media (min-width: $tablet-width) {
      grid-column-start: 5;
      grid-row-start: 1;
    }
  }

  &__develop {
    color: #fff;
  }

  &__info {
    font-size: 12px;
    line-height: 16px;
    color: #555555;
  }
}

.social-wrapper a {
  margin-right: 16px;

  &:hover {
    border-bottom: none;
  }

  &:hover path {
    transition: 0.3s;
    fill: #fff;
  }

  &:last-child {
    margin-right: 0;
  }
}
.footer-container {
  display: flex;
  flex-direction: column;
}
