@font-face {
  font-family: "Gilroy";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/Gilroy-Regular.woff2"), url("../../fonts/Gilroy-Regular.woff");
}
@font-face {
  font-family: "Gilroy";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/Gilroy-SemiBold.woff2"), url("../../fonts/Gilroy-SemiBold.woff");
}
@font-face {
  font-family: "Gilroy";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/Gilroy-Bold.woff2"), url("../../fonts/Gilroy-Bold.woff");
}
@font-face {
  font-family: "Gilroy";
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/Gilroy-Black.woff2"), url("../../fonts/Gilroy-Black.woff");
}
* {
  box-sizing: border-box;
}

body {
  font-family: "Gilroy", "Arial", sans-serif;
  margin: 0 auto;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
}

.button {
  font-weight: 600;
  border-radius: 4px;
  color: #ffffff;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
div {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.button--red {
  background-color: #ad2717;
}
.button--red:hover {
  transition: 0.3s;
  background-color: #8b1a0d;
}
.button--red:active {
  opacity: 0.6;
  transition: 0.3s;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-header {
  position: relative;
  position: absolute;
  width: 100%;
  display: flex;
  z-index: 9999;
  align-items: center;
}
@media (max-width: 767px) {
  .page-header {
    padding: 16px;
  }
}
@media (min-width: 768px) {
  .page-header {
    padding: 16px;
  }
}
@media (min-width: 1024px) {
  .page-header {
    padding: 20px 40px;
  }
}
@media (min-width: 768px) {
  .page-header__logo img {
    margin-right: 20px;
  }
}
@media (min-width: 1024px) {
  .page-header__logo img {
    width: 140px;
    height: 64px;
    margin-right: 40px;
  }
}
@media (min-width: 1242px) {
  .page-header__logo img {
    margin-right: 111px;
  }
}

@media (max-width: 767px) {
  .navigation-wrapper {
    position: fixed;
    top: 0;
    left: 13%;
    right: 0;
    display: flex;
    flex-direction: column;
    height: 70vh;
    padding: 100px 10px 50px 10px;
    font-size: 24px;
    line-height: 28px;
    background-color: #fff;
  }
  .navigation-wrapper--open {
    overflow-x: scroll;
    visibility: visible;
    opacity: 1;
    transform: translateX(0);
    transition: transform 0.5s, opacity 0.3s, visibility 0s;
  }
  .navigation-wrapper--close {
    visibility: hidden;
    pointer-events: none;
    opacity: 0;
    transform: translateX(100%);
    transition: transform 0.5s, opacity 0.3s, visibility 0s 0.3s;
  }
}

@media (min-width: 768px) {
  .navigation {
    display: flex;
    align-items: center;
  }
}
.navigation__list {
  display: flex;
}
@media (max-width: 767px) {
  .navigation__list {
    margin-bottom: 70px;
    flex-direction: column;
    font-weight: 700;
  }
}
@media (min-width: 768px) {
  .navigation__list {
    column-gap: 18px;
  }
}
@media (min-width: 850px) {
  .navigation__list {
    column-gap: 21px;
  }
}
@media (min-width: 950px) {
  .navigation__list {
    column-gap: 30px;
  }
}
@media (min-width: 1024px) {
  .navigation__list {
    column-gap: 20px;
  }
}
@media (min-width: 1200px) {
  .navigation__list {
    column-gap: 40px;
  }
}
.navigation__item {
  position: relative;
}
@media (max-width: 767px) {
  .navigation__item {
    width: 100%;
    text-align: right;
    margin-bottom: 16px;
    border-bottom: 1px solid #eeeeee;
  }
}
@media (max-width: 767px) and (min-width: 500px) {
  .navigation__item {
    text-align: center;
  }
}
@media (min-width: 768px) {
  .navigation__item {
    font-size: 12px;
    border-bottom: 1px solid transparent;
  }
  .navigation__item:hover {
    border-bottom: 1px solid #ffffff;
    transition: 0.3s;
  }
  .navigation__item:active {
    opacity: 0.6;
    transition: 0.3s;
  }
}
@media (min-width: 850px) {
  .navigation__item {
    font-size: 14px;
  }
}
@media (min-width: 1024px) {
  .navigation__item {
    font-size: 16px;
  }
}
@media (max-width: 767px) {
  .navigation__link {
    color: #000000;
  }
}
@media (min-width: 768px) {
  .navigation__link {
    font-weight: 700;
    color: #fff;
  }
}
.navigation__mobile-ticket-button {
  display: none;
}
@media (max-width: 767px) {
  .navigation__mobile-ticket-button {
    display: block;
    padding: 16px 70px;
    font-weight: 600;
    text-align: center;
    align-self: flex-end;
  }
}
@media (max-width: 767px) and (min-width: 500px) {
  .navigation__mobile-ticket-button {
    align-self: center;
  }
}
.navigation__buttons {
  display: flex;
  margin-left: auto;
  align-items: flex-start;
}
.navigation__blind-button {
  width: 32px;
  margin-right: 24px;
}
.navigation__blind-button:hover img:hover {
  outline: 1px solid #fff;
  border-radius: 50%;
}
.navigation__blind-button:hover img:active {
  opacity: 0.7;
  transition: 0.3s;
}
.navigation__ticket-button {
  padding: 8px 16px;
  font-size: 12px;
  line-height: 18px;
}
@media (max-width: 767px) {
  .navigation__ticket-button {
    display: none;
  }
}
.navigation__toggler {
  display: none;
}
@media (max-width: 767px) {
  .navigation__toggler {
    position: relative;
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    background-color: #000000;
    border-radius: 50%;
    border: 1px solid #fff;
    cursor: pointer;
  }
  .navigation__toggler span {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 18px;
    height: 2px;
    background-color: #fff;
  }
  .navigation__toggler::before, .navigation__toggler::after {
    content: "";
    position: absolute;
    width: 18px;
    height: 2px;
    background-color: #fff;
  }
  .navigation__toggler::before {
    top: 25%;
    transform: translateY(-25%);
  }
  .navigation__toggler::after {
    bottom: 25%;
    transform: translateY(25%);
  }
  .navigation__toggler.active span {
    transform: scale(0);
  }
  .navigation__toggler.active::before {
    top: 48%;
    transform: rotate(-45deg) translateY(-48%);
  }
  .navigation__toggler.active::after {
    bottom: 52%;
    transform: rotate(45deg) translateY(52%);
  }
}

.modal-covid {
  position: fixed;
  z-index: 998;
  background-color: #222;
  color: #fff;
  font-weight: 600;
  height: auto;
}
@media (max-width: 767px) {
  .modal-covid {
    right: 1%;
    bottom: 5%;
    max-width: 343px;
    padding: 16px;
    border-radius: 4px;
  }
}
@media (min-width: 768px) {
  .modal-covid {
    right: 20px;
    bottom: 5%;
    max-width: 308px;
    padding: 24px;
    border-radius: 12px;
  }
}
.modal-covid__close {
  position: absolute;
  top: 12px;
  right: 12px;
}
.modal-covid__text {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;
}
.modal-covid__button {
  cursor: pointer;
  border: 1px solid #fff;
  padding: 8px 24px;
  font-size: 12px;
  line-height: 16px;
}
.modal-covid__button:hover {
  opacity: 0.7;
}

@media (max-width: 767px) {
  .overlay--open {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
