    top: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
  }
}

@media (max-width: 767px) {
  .header-slider {
    margin-bottom: 40px;
  }
}
@media (min-width: 768px) {
  .header-slider {
    margin-bottom: 30px;
  }
}
@media (min-width: 1024px) {
  .header-slider {
    margin-bottom: 50px;
  }
}
@media (min-width: 1200px) {
  .header-slider {
    margin-bottom: 80px;
  }
}

.slider {
  width: 100%;
  position: relative;
}
.slider__slide {
  display: flex;
  flex-direction: column;
}
.slider__slide img {
  width: 100%;
