.page-header {
  position: relative;
  position: absolute;
  width: 100%;
  display: flex;
  z-index: 9999;
  align-items: center;

  @media (max-width: $mobile-only-width) {
    padding: 16px;
  }

  @media (min-width: $tablet-width) {
    padding: 16px;
  }

  @media (min-width: $desktop-width) {
    padding: 20px 40px;
  }

  &__logo img {
    @media (min-width: $tablet-width) {
      margin-right: 20px;
    }

    @media (min-width: $desktop-width) {
      width: 140px;
      height: 64px;
      margin-right: 40px;
    }

    @media (min-width: 1242px) {
      margin-right: 111px;
    }
  }
}
