.media-news {
  &__list {
    @media (max-width: $mobile-only-width) {
      margin-bottom: 20px;
    }
  }

  &__item {
    position: relative;
    padding: 14px 0;
    padding-right: 20px;
    border-bottom: 1px solid #eeeeee;

    &:hover .media-news__title {
      transition: 0.3s;
      color: #ad2717;
    }

    &:hover .media-news__link {
      transition: 0.3s;
      background-image: url("../../img/media/link-arrow.svg");
    }

    a {
      color: inherit;
    }

    @media (min-width: $desktop-ultra-width) {
      padding: 20px 0;
    }
  }

  &__category {
    display: inline-block;
    padding: 2px 12px;
    border-radius: 100px;
    background-color: #eeeeee;

    font-size: 12px;
    line-height: 16px;
    font-weight: 600;
    color: #bbbbbb;

    @media (max-width: $mobile-only-width) {
      margin-bottom: 8px;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 14px;
    }
  }

  &__title {
    font-weight: 600;

    padding-right: 15px;
    @media (max-width: $mobile-only-width) {
      font-size: 16px;
      line-height: 24px;

      margin-bottom: 8px;
    }

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 26px;
      margin-bottom: 8px;
    }

    @media (min-width: $desktop-width) {
      font-size: 24px;
      line-height: 28px;
    }
  }

  &__post-date {
    font-size: 12px;
    line-height: 16px;
    color: #cccccc;
  }

  &__link {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);

    background: url("../../img/media/link-arrow-white.svg") no-repeat;
    background-position: center;
    background-size: 24px 24px;
    width: 24px;
    height: 24px;

    @media (min-width: $desktop-ultra-width) {
      width: 40px;
      height: 40px;
      background-size: 40px 40px;
    }
  }
  //Блок карточки новости

  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;

    @media (max-width: $mobile-only-width) {
      gap: 17px;
    }

    @media (min-width: $tablet-width) {
      gap: 15px;
    }

    @media (min-width: $desktop-width) {
      gap: 28px;
    }
  }

  //Обертка карточки
  &__news-link {
    cursor: pointer;
    color: inherit;
    display: flex;
    flex-direction: column;

    .media-news__category {
      align-self: flex-start;
    }

    &:hover .media-news__title {
      color: #ad2717;
      transition: 0.3s;
    }

    &:hover img {
      filter: brightness(115%);
      box-shadow: 0 0 3px #444;
      transition: 0.3s;
    }
  }

  &__image {
    width: 100%;
    max-height: 352px;
    object-fit: cover;
    @media (max-width: $mobile-only-width) {
      margin-bottom: 14px;
      border-radius: 4px;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 22px;
      border-radius: 12px;
    }
  }
}
